.body {
    margin: 0 auto;
    max-width: 1100px;
    display: grid;
    grid-template-areas:
            "head head"
            "nav content";
    grid-template-rows: 40px 1fr;
    grid-template-columns: 2fr 10fr ;

}

.appContent {
    grid-area: content;
    background: #e8e8ef;
}


